.infinite-scroll-container {
    height: 585px;
    overflow-y: auto;
    border: 1px solid #ccc;
  }
  
  table {
    width: 100%;
    border-collapse: collapse;
  }
  
  thead th {
    position: sticky;
    top: 0;
    z-index: 100;
    padding: 8px;
  }

  th, td {
    border: 1px solid #ddd;
    padding: 5px;
    text-align: left;
  }
  
  th {
    background-color: #f4f4f4;
  }
  
  tbody tr:hover {
    background-color: #f1f1f1;
  }

.filter-container {
  display: flex;
  padding: 15px;
}

.filters {
  width: 250px;
  border: 1px solid black;
  border-radius: 8px;
  margin-right: 50px;
  padding: 5px;
}
select, input {
  border: none;
  width: 245px;
}

label {
  display: block;
  font-size: 12px;
}

.always-visible-spinner::-webkit-inner-spin-button,
.always-visible-spinner::-webkit-outer-spin-button, .always-visible-spinner{
    opacity: 1; 
    display: block;
    width: 245px;
}

input[type="range"] {
  width: 245px;
}
.spinner {
  width: 210px;
}

button {
  border: none;
}



